
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #fff100;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.container {
  position: relative;
  max-width: 720px;
  width: 10rem;
  height: 17.777778rem;
  background: url(../../static/img/bg.0251f9f.jpg) no-repeat;
  background-size: 100% 100%;
  margin: 0 auto;
}
a {
  text-decoration: none;
  color: #fff100;
}
header {
  position: relative;
  overflow: hidden;
  height: 7.3rem;
}
header video {
  width: 8.7rem;
  position: absolute;
  left: 50%;
  bottom: -0.3rem;
  -webkit-transform: translate(-50.6%, -5.6%);
          transform: translate(-50.6%, -5.6%);
  background: #000;
  height: 5.4rem;
}
main {
  position: relative;
  width: 100%;
}
main img {
  width: 7rem;
  position: absolute;
  left: 1.4rem;
  top: 0.52rem;
}
footer {
  position: relative;
  top: 1rem;
  width: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
footer .cd_arce {
  width: 65%;
  height: 2.5rem;
  margin: 0 auto;
  position: relative;
  top: 2.5rem;
}
footer .circle {
  position: absolute;
  top: 0;
  width: 2.625rem;
  height: 2.625rem;
  border-radius: 50%;
  -webkit-box-shadow: 1px 1px 12px 5px #fdd6003b;
          box-shadow: 1px 1px 12px 5px #fdd6003b;
  background: rgba(255, 255, 0, 0.349);
  -webkit-animation: leavl_move 5s cubic-bezier(0, 0, 0.2, 1) 0s infinite alternate;
          animation: leavl_move 5s cubic-bezier(0, 0, 0.2, 1) 0s infinite alternate;
}
footer .footer-bottom {
  position: relative;
}
footer span {
  color: #ffff4a;
  position: absolute;
  margin-left: -1.3rem;
  font-size: 0.5rem;
  top: 4rem;
}
footer .finger {
  background: url(../../static/img/finger.72619e3.png) no-repeat;
  background-size: 100% 100%;
  position: absolute;
  width: 0.87rem;
  height: 1.52rem;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  margin-left: -0.2rem;
  -webkit-animation: vertical_move 2s linear infinite;
          animation: vertical_move 2s linear infinite;
}
@-webkit-keyframes vertical_move {
0% {
    top: 3.5rem;
    left: 50%;
}
100% {
    top: 1.7rem;
    left: 40%;
}
}
@keyframes vertical_move {
0% {
    top: 3.5rem;
    left: 50%;
}
100% {
    top: 1.7rem;
    left: 40%;
}
}
@-webkit-keyframes leavl_move {
0% {
    left: 0;
}
100% {
    left: 4rem;
}
}
@keyframes leavl_move {
0% {
    left: 0;
}
100% {
    left: 4rem;
}
}
.balck-frame {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
}
.layer-container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
}
.layer-container ul {
  position: relative;
  list-style: none;
}
.layer-container ul li {
  text-align: left;
  position: absolute;
  top: 1rem;
  left: 1rem;
}
.layer-container ul li img {
  width: 3rem;
  height: 3rem;
}
.layer-container ul li:nth-child(2) {
  left: 6rem;
}
.layer-container ul li:nth-child(3) {
  top: 6rem;
}
.layer-container ul li:nth-child(4) {
  top: 6rem;
  left: 6rem;
}
.layer-container ul li:nth-child(5) {
  top: 11rem;
}
.layer-container ul li p {
  text-align: center;
}
.layer-container ul li p:first-of-type {
  margin-top: 0.3rem;
}
.share-containet {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
}
.share-containet .share-head {
  height: 5rem;
  padding-top: 2rem;
  font-size: 0.6rem;
}
.share-containet .share-head p {
  margin-bottom: 0.2rem;
}
.share-containet .share-btn .button {
  line-height: 1.8rem;
  margin: 0 auto 1rem;
  font-size: 0.5rem;
  color: #ff5152;
  border: 1px solid #ff5152;
  border-radius: 5px;
  width: 6.42rem;
  height: 1.8rem;
}
.share-containet .share-btn .active {
  background: #ff5152;
  color: #fff100;
}
.external_share_container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
}
.external_share_container p {
  font-size: 1.4rem;
  margin-top: 5rem;
}
.external_share_container div {
  width: 5px;
  height: 50px;
  background: yellow;
  position: absolute;
  display: block;
  right: 1rem;
  top: 1.5rem;
  -webkit-animation: upMove 1.5s linear 0s infinite;
          animation: upMove 1.5s linear 0s infinite;
}
.external_share_container div::before,
.external_share_container div::after {
  content: '';
  width: 5px;
  height: 30px;
  background: yellow;
  position: absolute;
  top: -2px;
}
.external_share_container div::before {
  left: -10px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.external_share_container div::after {
  right: -10px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
@-webkit-keyframes upMove {
0% {
    top: 1.5rem;
}
100% {
    top: 0.5rem;
}
}
@keyframes upMove {
0% {
    top: 1.5rem;
}
100% {
    top: 0.5rem;
}
}
